@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen','Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

@font-face{font-family:"Turret Road", cursive;src:url("https://fonts.googleapis.com/css2?family=Turret+Road:wght@800&display=swap")}.navigationBar{background-color:#303030;padding:.5rem}.navbarBrand{font-size:1.5rem !important;color:#0282f9 !important}.NavText{margin-left:4.5rem;font-family:'Press Start 2P', cursive}.navbarBrand img{-webkit-filter:grayscale(0%) !important;filter:grayscale(0%) !important}

@font-face{font-family:"Turret Road", cursive;src:url("https://fonts.googleapis.com/css2?family=Turret+Road:wght@800&display=swap")}.PiedPage{background-color:#303030;padding:4rem}

.row{display:flex;flex-wrap:wrap;padding:0 1rem}.col{flex:25% 1;max-width:30%;padding:0 1rem}img{margin-top:8px;vertical-align:middle;width:100%;-webkit-filter:grayscale(90%);filter:grayscale(90%)}img:hover{-webkit-filter:grayscale(0%);filter:grayscale(0%)}

@font-face{font-family:"Turret Road", cursive;src:url("https://fonts.googleapis.com/css2?family=Turret+Road:wght@800&display=swap")}.Banner{-webkit-filter:grayscale(0%) !important;filter:grayscale(0%) !important}

@font-face{font-family:"Turret Road", cursive;src:url("https://fonts.googleapis.com/css2?family=Turret+Road:wght@800&display=swap")}label{font-family:"Turret Road", cursive;text-align:center}@media screen and (max-width: 640px){.trans{transform:none !important}.row{display:block !important}}

@font-face{font-family:"Turret Road", cursive;src:url("https://fonts.googleapis.com/css2?family=Turret+Road:wght@800&display=swap")}h2,p{font-family:"Turret Road", cursive;text-align:center}.Carte{background-color:#a0999966 !important}.btn{font-family:"Turret Road", cursive;text-align:center}

